@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Advent+Pro&family=Roboto:wght@300&display=swap);
.NavMain {
  position: -webkit-sticky;
  position: sticky;
  background-color: transparent;
  left: 0;
  right: 0;
  color: white;
  background:black;
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .logo {
  font-size: 2.2rem;
  font-weight: 600;
  text-shadow: 2px 2px 2px grey;
  z-index: 1;
}

.NavMain ul {
  display: flex;
  align-items: center;
  margin-left: auto;
  font-weight: 600;
  font-size: 1.2rem;
}

.NavMain li {
  cursor: pointer;
  font-size: 1.2rem;
  margin: 0 1.5rem;
  font-weight: 600;
}

.NavMain li svg {
  margin-right: 0.5rem;
  -webkit-filter: invert(43%) sepia(88%) saturate(3991%) hue-rotate(224deg)
    brightness(98%) contrast(107%);
          filter: invert(43%) sepia(88%) saturate(3991%) hue-rotate(224deg)
    brightness(98%) contrast(107%);
  position: relative;
  top: 0.4rem;
}

.NavMain ul li:not(:last-child) {
  margin-right: 1.5em;
}

.LocationAutoComplete {
  position: relative;
}

.LocationAutoComplete-results {
  position: absolute;
  background-color: white;
  padding: 0;
  margin: 0;
  border: 1px solid #00000011;
}

.LocationAutoComplete-items {
  list-style: none;
  border: 1px solid #00000011;
  cursor: pointer;
  padding: 10px 10px;
}

.LocationAutoComplete-items:hover {
  background-color: #0000ff11;
}

.Home .title-container {
  background: black;
  transform: skew(-20deg);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0 auto;
  color:white;
}

.Home h1 {
  text-align: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 60px auto;
  font-size: 4rem;
  padding: 20px;
}

.Home h2 {
  font-size: 1.3rem;
  color: white;
}

.Home .map-result {
  width: 30vw;
  margin: 0 20px;
  background: #7f7f7e;
  /* color:white; */
}

.Home .LocationAutoComplete {
  position: fixed;
  right: 20px;
  width: calc(30vw - 10px);
}

.Home .LocationAutoComplete input {
  width: 100%;
  box-sizing: border-box;
  border: none;
  padding: 16px;
  font-size: 1.2rem;
  box-shadow: 0 0.125rem 0.75rem rgba(0, 0, 0, 0.08);
}

.Home .map-result {
  padding: 16px;
  margin-top: 50px;
}

.Home .map-result a {
  text-decoration: underline;
}

.form-section {
  max-width: 550px;
  margin: 3vh auto 0;
  padding: 0 20px;
  max-height: 75vh;
}

.form-section .header {
  text-align: center;
}

.form .title {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.form {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: rgba(16, 35, 82, 0.02) 0 7px 50px 20px;
}

.form .message {
  display: flex;
  flex-direction: row;
  background-color: #f1f2ff;
  padding: 15px;
  width: 94%;
  border-radius: 10px;
  color: #5663ff;
  font-size: 14px;
  font-weight: 500;
}

.form-group {
  margin-bottom: 20px;
}

.form-group .custom-upload {
  border: 1px solid #5663ff;
  color: #5663ff;
  border-radius: 4px;
  display: inline-block;
  padding: 10px 22px;
  cursor: pointer;
  margin-bottom: 20px;
}

.form-group select {
  display: block;
  font-size: 14px;
  line-height: 1.3;
  padding: 12px 12px;
  width: 100%;
  box-sizing: border-box;
  margin: 10px 0;
  border-radius: 4px;
  border: 0;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: #eff1f6;
  background-image: url("/assets/arrow.svg");
  background-repeat: no-repeat, repeat;
  background-position: right 18px top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}

.form-group .input {
  margin: 8px 0 12px;
  width: 100%;
  border-radius: 4px;
  display: block;
  border: none;
  font-size: 14px;
  background: #eff1f6;
  padding: 12px 12px;
}

.form-group .text-area {
  display: block;
  font-size: 14px;
  width: 100%;
  background: #eff1f6;
  border: none;
  border-radius: 4px;
  line-height: 1.3;
  resize: none;
  box-sizing: border-box;
  padding: 12px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Manrope", sans-serif;
}

.form-group .label {
  font-weight: 600;
}

.form-group .input[type="file"] {
  display: none;
}

.form-group .custom-upload {
  border: 1px solid #5663ff;
  color: #5663ff;
  border-radius: 4px;
  display: inline-block;
  padding: 10px 22px;
  cursor: pointer;
  margin-bottom: 20px;
}

.form button {
  margin-top: 10px;
  background: black;
  width: 100%;
  color: white;
  padding: 16px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
}

.form-section-bottom {
  text-align: center;
}

.form .for-accessibility {
  display: none;
}

.form input[type="radio"],
.form input[type="checkbox"] {
  margin: 0 5px;
}

.form .unit-numbers {
  margin-left: 5px;
}

.form .unit-numbers input {
  width: 50px;
}

.form .unit-numbers input::-webkit-input-placeholder {
  text-align: center;
}

.Profile {
  max-width: 800px;
  margin: 10vh auto 0;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}

.Profile .user-contact {
  margin-top: 20px;
  background: #f1f2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 10px;
}

.Profile .user-image {
  margin: 0px auto;
  height: 200px;
  width: 200px;
}

.Profile .user-image.round-image {
  height: 250px;
  width: 250px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}

.Profile .user-presentation {
  text-align: center;
}

.Profile .user.contact {
  margin-top: 20px;
  background: #f1f2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 10px;
}

.Profile .user.contact h4 {
  text-align: center;
  margin-top: 0;
}

.Profile .form-group .input {
  margin-top: 8px;
  width: 350px;
  border-radius: 4px;
  display: block;
  border: 1px solid #5663ff;
  font-size: 16px;
  background: transparent;
  padding: 12px 12px;
}

.Profile .form-group .label {
  font-size: 14px;
  font-weight: 600;
}

.CardItem {
  margin-top: 50px;
}

.CardItem .item {
  display: flex;
  padding: 20px 40px;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  background: white;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.CardItem .round-image {
  height: 200px;
  width: 200px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}

.CardItem .item-empty {
  margin-top: 60px;
  display: flex;
  padding: 40px 40px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.CardItem .item-empty button {
  margin-top: 20px;
  width: 100%;
  background: #5663ff;
  color: white;
  font-size: 16px;
  padding: 12px 16px;
  border-radius: 10px;
  border: none;
}

.CardItem .round-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.CardItem .description {
  flex: 2 1;
  text-align: center;
}

.CardItem .description h2 {
  margin: 10px 0;
}
.CardItem .description h2 {
  margin: 5px 0;
  font-weight: 400;
}

.CardItem .buttons {
  display: flex;
}

.CardItem > h3 {
  text-align: center;
}

.OneHouse {
  margin: 100px auto;
  width: 40vw;
}

.OneHouse .form {
  margin: 50px 0;
}

.OneHouse .buttons button:first-child {
  margin-right: 5px;
}

.OneHouse .buttons button:last-child {
  margin-left: 5px;
}

.OneHouse .rating-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.OneHouse .rating-container img {
  width: 100px;
}

.OneHouse [type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.OneHouse [type="radio"] + img {
  cursor: pointer;
}

.OneHouse [type="radio"]:checked ~ img {
  border: 5px solid #5663ff;
  border-radius: 100px;
}

.OneHouse .ratingSelector {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.OneHouse input[type="file"] {
  display: none;
}

.OneHouse .custom-file-upload {
  background: #eff1fb;
  width: 100%;
  padding: 16px;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}

.OneHouse .spaced-t {
  margin-top: 30px;
}

.OneHouse .spaced-b {
  margin-bottom: 20px;
}

.OneHouse textarea {
  width: 100%;
}

.OneHouse ul {
  list-style-type: square;
  padding-left: 20px;
}

.review {
  margin: 20px 0;
  background: #eff1fb;
  border-radius: 10px;
  padding: 16px;
}

.FormHouse {
  margin: 100px auto;
  width: 40vw;
}

