* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  background: #f8f9fd;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

/* Typography */

h2 {
  font-size: 1.1rem;
}

.link {
  /* color: white; */
  font-weight: 700;
}

.flex {
  display: flex;
}

.align-c {
  align-items: center;
}

