@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Advent+Pro&family=Roboto:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  background: #f8f9fd;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

/* Typography */

h2 {
  font-size: 1.1rem;
}

.link {
  /* color: white; */
  font-weight: 700;
}

.flex {
  display: flex;
}

.align-c {
  align-items: center;
}
